<script>
import Toastify from 'toastify-js'
import {useRouter} from 'vue-router'
import {onMounted, ref, defineComponent, computed} from "vue";
import CategoriesService from "../services/categories.service";
import {useStore} from 'vuex'
import BooksService from "@/services/books.service";


export default defineComponent({
  props: ['reload'],
  setup: function () {

    const store = useStore()
    const router = useRouter()
    const products = ref()
    const categories = ref()
    const cart = ref([])
    const goToCart = () => {
      router.push("/halfprice_cart")
    }
    const goToShop = () => {
      router.push("/halfprice_shop")
    }
    const goToHome = () => {
      router.push("/")
    }
    const goToAbout = () => {
      router.push("/halfprice_about")
    }
    const goToContact = () => {
      router.push("/halfprice_contact")
    }
    const addToCart = (book) => {

      book.quantity++
      store.commit('ADD_TO_CART', book)
      Toastify({
        text: "successfully added to cart",
        duration: 3000,
        newWindow: true,
        close: true,
        gravity: "top",
        position: "right",
        stopOnFocus: true,
      }).showToast();
      console.log(books)
      console.log(books_in_cart)
    }
    const books_in_cart = computed(() =>
        store.getters.getTotalCartItems
    )

    const removeFromCart = (book) => {
      store.commit('REMOVE_FROM_CART', book)
    }

    const fetchBooks = async () => {
      BooksService.fetchBooks().then(response => {
        console.log(response.data)
        products.value = response.data
        console.log(products.value)
        let newProducts = products.value.forEach(product  =>
            product.quantity = 0
        )
        console.log(newProducts , "why?")
      })
    }
    const fetchCategories = async () => {
      CategoriesService.fetchCategories().then(response => {
        console.log(response.data)
        categories.value = response.data
      })

    }
    const books = computed(() =>
        store.getters.getCartItems)

    const subtotal = computed(() =>
        store.getters.getTotalCost
    )


    onMounted(() => {
      if (localStorage.getItem('reloaded')) {
        // The page was just reloaded. Clear the value from local storage
        // so that it will reload the next time this page is visited.
        localStorage.removeItem('reloaded');
      } else {
        // Set a flag so that we know not to reload the page twice.
        localStorage.setItem('reloaded', '1');
        location.reload();
      }

      fetchCategories()
      fetchBooks()


    })
    // watch (
    //     ()=> props.reload,
    //     ()=> {
    //       if(props.reload == true){
    //
    //         window.location.reload()
    //       }
    //     }
    // )


    return {
      router,
      books,
      removeFromCart,
      fetchCategories,
      categories,
      products,
      cart,
      addToCart,
      subtotal,
      books_in_cart,
      goToHome,
      goToShop,
      goToCart,
      goToAbout,
      fetchBooks,
      goToContact,

    }
  }
})
</script>